/*eslint-disable*/
import React, {useState, useEffect} from 'react';
import {useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';
let 박스 = styled.div`
  padding:20px;
`;
let 제목=styled.h4`
font-size:25px;
color:${props=>props.색상}
`;
function Detail(props){
  let [알림표시, 알림제거]=useState(true)
  let [입력, 입력변경]=useState('') 

    let {id}=useParams();
    let 찾은상품=props.shoes.find(function(상품){
      return 상품.id==id
    })
    let history = useHistory();

    useEffect(()=>{
      //2초 후에 alert 제거
      let 타이머 =setTimeout(()=>{
        알림제거(false);     
      },2000)
      return ()=>{clearTimeout(타이머)}//Effect에서 타임아웃 쓸때 return 해줘야 오류가 나지 않은ㅁ
    },[알림표시]);//[]조건을 넣는 부분 아무것도 안넣으면 1회만
    return(
  <div className="container">
        <div className="row">
          <박스>
          <제목 className="red">detail</제목>
          </박스>
         
          <input onChange={(e)=>{입력변경(e.target.value)}}/>
          {
            알림표시===true
            ?<알림></알림>
            :null
          }
         
          <div className="col-md-6">
            <img src={"https://codingapple1.github.io/shop/shoes1.jpg"} width="100%" />
          </div>
          <div className="col-md-6 mt-4">
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price}원</p>
          
          <Info 재고={props.재고} ></Info>
            <button className="btn btn-danger" onClick={()=>{props.재고변경(재고-1)}}>주문하기</button>
            &nbsp;
            <button className="btn btn-danger" onClick={()=>{
                //history.goBack();
                history.push('/');
            }}>뒤로가기</button> 
          </div>
        </div>
  </div> 
    )
  }

  function Info(props){
    return(
      <p>재고 : {props.재고[0]}</p>
    )
  }

  function 알림(){
    return(
    <div className="my-alert2">
    <p>재고가 얼마 남지 않았습니다.</p>
  </div>
    )
  }
  export default Detail;